body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #2b2f77;
  background: radial-gradient(circle, #2b2f77 0%, #141852 50%, #070b34 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container-robots {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-around;
}

h1 {
  color: aliceblue;
  background-color: lightblue;
  width: 200px;
  padding: 0.5em 1em;
  border-radius: 92% 8% 96% 4% / 8% 94% 6% 92%;
}

.container-input {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.spinner-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  flex-wrap: nowrap;
}
.spinner {
  position: relative;
  top: 30vh;
  margin: 3em auto;
  --d: 24.6px;
  width: 4.5px;
  height: 4.5px;
  border-radius: 50%;
  color: #474bff;
  box-shadow: calc(1 * var(--d)) calc(0 * var(--d)) 0 0,
    calc(0.707 * var(--d)) calc(0.707 * var(--d)) 0 1.1px,
    calc(0 * var(--d)) calc(1 * var(--d)) 0 2.2px,
    calc(-0.707 * var(--d)) calc(0.707 * var(--d)) 0 3.4px,
    calc(-1 * var(--d)) calc(0 * var(--d)) 0 4.5px,
    calc(-0.707 * var(--d)) calc(-0.707 * var(--d)) 0 5.6px,
    calc(0 * var(--d)) calc(-1 * var(--d)) 0 6.7px;
  -webkit-animation: spinner-a90wxe 1s infinite steps(8);
          animation: spinner-a90wxe 1s infinite steps(8);
}

@-webkit-keyframes spinner-a90wxe {
  100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
  }
}

@keyframes spinner-a90wxe {
  100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
  }
}

.card {
  width: 315px;
  height: 350px;
  margin-bottom: 1em;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.scroll {
  margin: 1em;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-around;

  overflow-x: hidden;
  overflow-y: scroll;

  /* border: 2px solid black; */
  height: 400px;
  width: 80%;
}

.bloob {
  padding: 1em;
  border-radius: 92% 8% 96% 4% / 8% 94% 6% 92%;
}

